import { Pipe, PipeTransform } from '@angular/core';
import { FilterParameter, ListItem } from '../list-form/list-form.component';

@Pipe({
  name: 'filterItems',
  pure: false,
})
export class FilterItemsPipe implements PipeTransform {
  transform(listItems: ListItem[], filter: FilterParameter): ListItem[] {
    return listItems.filter((item) => item[filter.key] === filter.value);
  }
}
