import { Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
export interface ListItem {
  text: string;
  isCompleted: Boolean;
  id: number;
  itemTopicality: boolean;
}
export interface FilterParameter {
  key: string;
  value?: boolean;
}
@Component({
  selector: 'app-list-form',
  templateUrl: './list-form.component.html',
  styleUrls: ['./list-form.component.scss'],
})
export class ListFormComponent implements OnInit {
  showListContainer = false;
  listItemText = '';
  windowHeight: number;
  counterItemsWithoutScroll = 0;
  listItems: ListItem[] = [];
  filterParameter: FilterParameter = { key: 'itemTopicality', value: true };
  counterToDoTasks = this.listItems.length;
  @ViewChild('toDo_container', { static: false }) toDoContainer: ElementRef;
  @ViewChild('items', { static: false }) items: ElementRef;

  focusOut() {
    this.listItemText = '';
  }
  addListItem() {
    if (this.listItemText.trim()) {
      let item = {
        text: this.listItemText,
        isCompleted: false,
        id: this.listItems.length,
        itemTopicality: true,
      };
      this.listItems.unshift(item);
      this.counterToDoTasks = this.counterToDoTasks + 1;
      this.listItemText = '';
      let heightContainerList = this.toDoContainer.nativeElement.offsetHeight;
      console.log(this.listItems);
      if (heightContainerList >= this.windowHeight - 30) {
        console.log('!!!!!!!!!!!!!!!!!! висота перевищена');
        this.items.nativeElement.style.overflowY = 'scroll';
      } else {
        this.counterItemsWithoutScroll = this.listItems.length;
      }
    }
  }

  refreshCounter(counter: number) {
    this.counterToDoTasks = counter;
  }

  removeListItem(id) {
    if (!this.counterToDoTasks) this.counterToDoTasks = 0;

    this.listItems = this.listItems.filter((item) => {
      return item.id !== id;
    });

    this.counterToDoTasks = this.listItems.filter((item) => {
      return item.isCompleted === false;
    }).length;

    if (this.listItems.length <= this.counterItemsWithoutScroll) {
      console.log('!!!!!!!!!!!!!!!!!! висота допустима');
      this.items.nativeElement.style.overflowY = 'hidden';
    }
  }

  showAllItems() {
    this.filterParameter = {
      key: 'itemTopicality',
      value: true,
    };
    console.log(this.listItems);
  }
  showCompletedItems() {
    this.filterParameter = {
      key: 'isCompleted',
      value: true,
    };
    console.log(this.listItems);
  }

  showNotCompletedItems() {
    this.filterParameter = {
      key: 'isCompleted',
      value: false,
    };
    console.log(this.listItems);
  }

  ngOnInit() {
    this.windowHeight = window.innerHeight;
  }
  // Изменение размера окна
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.windowHeight = event.target.innerHeight;
    console.log(this.windowHeight);
  }
}
